/**
 * External dependencies
 */
import { useEffect, useState, useCallback } from "@wordpress/element";
import { SelectControl, TextareaControl } from "@wordpress/components";
import { __ } from "@wordpress/i18n";
import { useSelect, useDispatch } from "@wordpress/data";
import { debounce, join } from "lodash";

/**
 * Internal dependencies
 */
import { options } from "./options";
import { countries } from "./countries";

export const Block = ({ checkoutExtensionData, extensions }) => {
  const isCalculating = useSelect((select) => {
    const store = select("wc/store/checkout");
    return store.isCalculating();
  });
  const { setExtensionData } = checkoutExtensionData;
  const debouncedSetExtensionData = useCallback(
    debounce((namespace, key, value) => {
      setExtensionData(namespace, key, value);
    }, 1000),
    [setExtensionData]
  );

  const validationErrorId = "smart-send-other-value";

  const { setValidationErrors, clearValidationError } = useDispatch(
    "wc/store/validation"
  );
  const validationError = useSelect((select) => {
    const store = select("wc/store/validation");
  
    return store.getValidationError(validationErrorId);

  });
  const [
    selectedPickupPoint,
    setselectedPickupPoint,
  ] = useState("try-again");
  useEffect(() => {
    var street = setValue("shipping-address_1");
    var city = setValue("shipping-city");
    var country = setValue("components-form-token-input-0");
    country = countries[country];
    var postcode = setValue("shipping-postcode");
    var selected = jQuery(".wc-block-components-shipping-rates-control")
    .find("input[type=radio]:checked")
    .val();

    if (isCalculating) {
      if (
        postcode !== null &&
        street !== null &&
        city !== null &&
        country !== null
      ) {
        findClosestAgentByAddress(selected,country, postcode, city, street);
      }
    }
  }, [isCalculating]);

  useEffect(() => {
    setExtensionData(
      "smart-send-logistics",
      "selectedPickupPoint",
      selectedPickupPoint
    );
  }, [setExtensionData, selectedPickupPoint]);

  const [hasInteractedWithOtherInput, setHasInteractedWithOtherInput] =
    useState(false);

  useEffect(() => {

    if (
      setselectedPickupPoint !== "other"
    ) {
      if (validationError) {
        clearValidationError(validationErrorId);
      }
      return;
    }
    setValidationErrors({
      [validationErrorId]: {
        message: __("Please add some text", "smart-send-logistics"),
        hidden: !hasInteractedWithOtherInput,
      },
    });

  }, [
    clearValidationError,
    setselectedPickupPoint,
    setValidationErrors,
    validationErrorId,
    debouncedSetExtensionData,
    validationError,
  ]);

  return (
    <div className="wp-block-smart-send-not-at-home">

      <SelectControl
        label={__("TLS Delivery Point", "smart-send-logistics")}
        value={selectedPickupPoint}
        options={options}
        onChange={setselectedPickupPoint}
        className="shiiping_smart_ar_hide"
      />
    </div>
  );
};



// Function to find closest agent by address
async  function findClosestAgentByAddress(ss_agent, country, postalCode, city, street) {

  country = country;
  postalCode = postalCode;
  city = city;
  street = street;
  var carrier= await getShippingCarrier(ss_agent);
  getPickupPoints(carrier, country, postalCode, city, street);
}

function setValue(id) {
  var element = document.getElementById(id);
  if (element) {
    return element.value;
  }
  return null;
}

const getPickupPoints = async (
  carrier,
  country,
  postalCode,
  city,
  street
) => {
  const url = "/wp-json/smart-send-logistics/v1/get-closest-pickup-points"; // Your endpoint URL

  const data = {
    country: country,
    postCode: postalCode,
    city: city,
    street: street,
    carrier: carrier
  };

  try {
    const response = await fetch(url, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data),
    });

    if (!response.ok) {
      const errorData = await response.json();
      jQuery(".shiiping_smart_ar_hide").hide();
      jQuery(".shiiping_smart_ar_hide").css("opacity", 0);
    } else {
      const results = await response.json();
      const sortedData = Object.entries(results).sort((a, b) => {
        const distanceA =
          a[0] === "0"
            ? 0
            : parseFloat(
                a[1].split(":")[0].replace("km", "").replace("m", "")
              ) * (a[1].includes("km") ? 1000 : 1);
        const distanceB =
          b[0] === "0"
            ? 0
            : parseFloat(
                b[1].split(":")[0].replace("km", "").replace("m", "")
              ) * (b[1].includes("km") ? 1000 : 1);
        return distanceB - distanceA;
      });
      var reversedrestoreddata = sortedData.reverse();
      var output = "";
      jQuery.each(reversedrestoreddata, function (key, value) {
        output += '<option value="' + value[0] + '">' + value[1] + "</option>";
      });
      jQuery(".shiiping_smart_ar_hide").find("select").html(output);
      getSelectedShippingMethod();
    }
  } catch (error) {
    console.log(error);
    alert("Failed to fetch pick-up points");

  }
};

function getSelectedShippingMethod() {
  var selected = jQuery(".wc-block-components-shipping-rates-control")
    .find("input[type=radio]:checked")
    .val();
  if (selected.indexOf("smart_send") !== -1) {
    jQuery(".shiiping_smart_ar_hide").show();
    jQuery(".shiiping_smart_ar_hide").css("opacity", 1);
    return selected;
  } else {
    jQuery(".shiiping_smart_ar_hide").hide();
    jQuery(".shiiping_smart_ar_hide").css("opacity", 0);
  }
}


const getShippingCarrier = async (
 shipping_method
) => {
  const url = "/wp-json/smart-send-logistics/v1/get-shipping-carrier"; // Your endpoint URL

  const data = {
    ss_method: shipping_method,
  };

  try {
    const response = await fetch(url, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data),
    });

    if (!response.ok) {
      const errorData = await response.json();
      console.log(errorData);
    } else {
      const results = await response.json();
      return results.carrier;
    }
  } catch (error) {
    console.log(error);
    alert("Failed to fetch Shipping Carrier");

  }
};